import axios from 'axios'

const baseUrl = 'https://api.mou.science/daily/v1';

function get(url) {
  return new Promise((resolve, reject) => {
    axios.get(baseUrl + url).then(response => {
      if (response.data) {
        resolve(response.data)
      } else {
        reject(response)
      }
    })
  })
}


export default {
  getDay(dateStr) {
    return get(`/day/${dateStr}`)
  },
  getArticle(id) {
    return get(`/article/${id}`)
  }
}



